import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MblogService } from '../services/middle/mblog.service';

@Component({
  selector: 'app-blogs',
  templateUrl: './blogs.component.html',
  styleUrls: ['./blogs.component.scss']
})

export class BlogsComponent implements OnDestroy, OnInit {
  blogs: any;
  unBlogs: any;
  constructor( private midBlog: MblogService, private router: Router ) {
    this.unBlogs = this.midBlog.emitBlogs.subscribe(cmp => {
      this.blogs = cmp;
    });
  }

  ngOnInit(): void {
    this.midBlog.blog();
  }

  readBlog(blog) {
    const blogName = blog.h5.split(' ').join('-');
    this.router.navigate([`/blog/${blogName}`, { i: JSON.stringify(blog.id) }]);
  }

  ngOnDestroy(){
    this.unBlogs.unsubscribe();
  }

}
